#first-section {
  position: relative; }
  #first-section::after {
    background: linear-gradient(45deg, white 25%, rgba(255, 255, 255, 0) 25%), linear-gradient(315deg, white 25%, rgba(255, 255, 255, 0) 25%);
    background-repeat: repeat-x;
    background-position: left bottom;
    background-size: 50px 100px;
    content: '';
    display: block;
    width: 100%;
    height: 100px;
    position: absolute;
    bottom: 0px;
    left: 0px; }

