*,
*::after,
*::before {
  box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700&subset=cyrillic');

html,
body {
  padding: 0;
  margin: 0;
  font-family: 'Open Sans';
}

.separator {
  width: 100%;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2rem 0;

  &::after {
    content: ' ';
    width: 10%;
    height: 4px;
    background: black;
  }
}

i {
  width: 1.5rem !important;
  height: 1.5rem !important;
  margin-right: 0.4rem;
}
