$bg: #c0c0c003;
$primary: #ffd700;
$highlight: #ffd70077;
$primaryText: black;

$grey: rgba(192, 192, 192, 0.3);
$important: rgba(255, 0, 0, 0.5);

$sectionBg: white;

$shadow: 0 0 30px rgba(0, 0, 0, 0.05);

a {
  // text-decoration: none;
  color: #333;
  position: relative;

  &:hover {
    color: #777;
  }
}

.ReactModal__Overlay {
  background-color: rgba(0, 0, 0, 0.1) !important;
  z-index: 1000;
}

.ReactModal__Content {
  z-index: 1000;
  opacity: 0;
  transform: translateY(450px);
  // width: 40%;
  border: none !important;
  left: 0% !important;
  right: 0% !important;
  top: 30% !important;
  bottom: 30% !important;
  height: 20rem;

  font-size: 2rem;

  @media screen and(min-width: 768px) {
    left: 25% !important;
    right: 25% !important;
    top: 25% !important;
    // bottom: 33% !important;
  }
}

.ReactModal__Content--after-open {
  opacity: 1;
  transform: translateY(0);
  transition: transform 250ms;
}

.ReactModal__Content--before-close {
  transform: translateY(-1000px);
}

img .circle {
  overflow: hidden;
}

.App {
  display: flex;
  flex-direction: column;
  width: calc(100vw - (100vw - 100%));
  padding: 0 auto;
  background: $bg;
  min-height: 100vh;
  overflow-x: hidden;
  // padding: 0 2rem 2rem;
}

.team-desc {
  font-size: 1.625rem;
  @media screen and(min-width: 768px) {
    font-size: 2rem;
  }
}

.section-title {
  font-size: 1.6rem;
  font-weight: bold;
  text-align: center;

  @media screen and(min-width: 768px) {
    font-size: 2.6rem;
  }
}

.title {
  margin-left: 0.625rem;
  font-weight: bold;
  // font-size: 1.5rem;
}

code {
  font-family: Arial, Helvetica, sans-serif;
}

.list-title {
  display: flex;
  align-items: center;
}

.underline {
  position: relative;
  &::after {
    content: ' ';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 50%;
    background: $highlight;
  }
}

.modal {
  font-size: 1.325rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.interest-choice {
  font-size: 1rem;

  input {
    width: auto;
    margin-right: 0.325rem;
    color: black;
  }
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  align-self: flex-end;
  margin-top: 2rem;
  & button {
    font-size: 1rem;
  }
  & > button:first-child {
    margin-right: 1rem;
  }
}

input {
  width: 100%;
  background: white;
  padding: 0.625rem;
  border-radius: 7px;
  box-shadow: none;
  outline: none;
  border: 2px solid $grey;
  color: black;
  font-size: inherit;

  &:active,
  &:focus {
    border: 2px solid $primary;
  }
}

section {
  // padding: 4rem;
  // box-shadow: $shadow;
  border-radius: 8px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

button {
  z-index: 1;
  border-radius: 8rem;
  padding: 0.725rem 1rem;
  font-size: inherit;
  border: none;
  font-size: inherit;
  color: $primaryText;
  background: $primary;
  outline: none;
  transition: transform 200ms ease-in-out;
  cursor: pointer;

  &:hover {
    transform: scale(1.03);
  }

  &.outlined {
    background: transparent;
    border: 1px solid $primary;
    color: $primary;
  }
}

.list-title {
  font-size: 1.625rem;
  font-weight: bold;
}
ul {
  width: 100%;
  list-style: none;
  padding-left: 1rem;
}

li {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    left: -15px;
    width: 10px;
    top: 50%;
    bottom: 50%;
    height: 3px;
    background: $primary;
  }
}

.text {
  // display: flex;
  // flex-direction: column;
  margin: 1rem 0;
  text-align: center;
  font-size: inherit;
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 400px;
  font-size: 1rem;

  & > button {
    width: 100%;
  }

  @media screen and(min-width: 768px) {
    width: 50%;
  }
}

.circle {
  background: $highlight;
  border-radius: 50%;
  width: 120px;
  height: 120px;
}

.separator-bottom {
  bottom: 0;

  width: 100%;
  z-index: 4;
  position: absolute;
  left: 0;
  right: 0;
  overflow: hidden;
}

.first-section {
  margin-top: 0rem;
  font-size: 1rem;
  position: relative;

  @media screen and(min-width: 768px) {
    flex-direction: row;
  }

  &.desc {
    background: white;
    flex: 1;
    padding: 2rem;
    color: black;
    display: flex;
    flex-direction: column;
  }
  &.contact {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 2rem;
    color: black;
    flex: 2;
    font-size: 2rem;

    // background-image: url('https://images.unsplash.com/photo-1530989241-3685c096e725?ixlib=rb-0.3.5&s=6ca4606d3ae5c4166952467562d15283&auto=format&fit=crop&w=1052&q=80');
    // background-size: cover;

    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      background: black;
      background-image: url('https://images.unsplash.com/photo-1518355077561-4af7abce973d?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=d6b1841d142d8afd0535f9f91f6bd551&auto=format&fit=crop&w=1502&q=80');
      background-size: cover;
      width: 100%;
      height: 100%;
      opacity: 0.9;
      z-index: -1;
    }
  }

  & > button {
    align-self: center;
    font-size: 1.325rem;
    margin-top: 1rem;
  }

  &.about {
    flex: 1;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    // justify-content: center;
    padding-top: 4rem;
  }
}

#first-section {
  position: relative;

  &::after {
    background: linear-gradient(
        45deg,
        rgb(255, 255, 255) 25%,
        rgba(255, 255, 255, 0) 25%
      ),
      linear-gradient(
        315deg,
        rgb(255, 255, 255) 25%,
        rgba(255, 255, 255, 0) 25%
      );
    background-repeat: repeat-x;
    background-position: left bottom;
    background-size: 50px 100px;
    content: '';
    display: block;

    width: 100%;
    height: 100px;

    position: absolute;
    bottom: 0px;
    left: 0px;
  }
}

#announcement-section {
  // justify-content: center;
  // align-items: center;
  padding: 2rem;
  min-height: 0;

  & .card {
    justify-content: flex-start;
    margin: 2rem 0;
    border-radius: 0;
    text-align: center;
    height: 450px;
    align-items: center;
    font-size: 1.625rem;
    font-weight: bold;

    & strong {
      font-weight: 700;
      color: rgb(137, 137, 137);
      font-family: Oswald;
      font-size: 4rem;
    }

    color: #333;

    &-1 {
      background: rgb(240, 240, 240);
    }
    &-2 {
      background: rgba(219, 219, 219, 0.77);
    }
    &-3 {
      background: rgb(207, 207, 207);
    }
  }
}

.card-title {
  font-weight: bold;
  font-size: 1.625rem;
  align-self: flex-start;
  margin-bottom: 0.325rem;
}

#vip-section {
  padding: 2rem;
  padding-top: 0;
  min-height: 0;

  & > article {
    margin-top: 2rem;

    & .list-title {
      margin-top: 1rem;
    }
  }

  & .card {
    & img {
      width: 102px;
      height: 102px;
    }

    @media screen and (min-width: 768px) {
      // height: 320px;
    }
  }
}

.cards {
  display: flex;
  flex-direction: column;
  margin: 0rem;
  align-items: center;
  justify-content: center;
  @media screen and(min-width: 768px) {
    flex-direction: row;
    margin: 0;
    & > .card:not(:last-child) {
      margin-right: 2rem;
    }
  }
}

.card {
  // box-shadow: $shadow;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  max-width: 300px;
  flex-direction: column;
  padding: 2rem;
  transition: all 400ms ease-in-out;
  width: 100%;
  margin-bottom: 1rem;

  @media screen and(min-width: 768px) {
    width: auto;
    margin-bottom: 0;
  }
}

.card-text {
  text-align: center;
}

#team-section {
  padding: 2rem;

  & > .cards {
    justify-content: center;
  }
}

.team-member {
  padding: 2rem;
  margin: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  flex: 1;
  background: $grey;

  & > .name {
    font-weight: bold;
    margin: 0.325rem 0;
  }
  & > .role {
    margin-bottom: 0.325rem;
  }

  &__desc {
    text-align: center;
  }
}

.last-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 4rem 0;
  height: 70vh;
}
